<template>
  <div style="width: 256pxÔºõ">
    <a-menu
      :selected-keys="selectedKeys"
      :open-keys.sync="openKeys"
      mode="inline"
      @openChange="onOpenChange"
      :theme="theme"
    >
      <template v-for="item in menuData">
        <a-menu-item
          :key="item.path"
          v-show="item.meta.isShow"
          @click="() => $router.push({ path: item.path })"
        >
          <svg-icon :icon-class="item.meta.icon" :scale="1" style="margin-right:10px"></svg-icon>
          <span v-if="!collapsed">{{ item.name }}</span>
        </a-menu-item>
        <!-- <sub-menu v-else :key="item.path" :menu-info="item" /> -->
      </template>
    </a-menu>
  </div>
</template>

<script>
// import SubMenu from "./SubMenu.vue";
export default {
  components: {
    // "sub-menu": SubMenu
  },
  props: {
    theme: {
      type: String,
      default: "dark"
    },
    collapsed: {
      type: Boolean,
    }
  },
  data() {
    this.selectedKeysMap =
      JSON.parse(sessionStorage.getItem("selectedKeysMap")) || {}; //Ë∑ØÁî±ÂíåÈÄâ‰∏≠ËèúÂçïkeyÁöÑÊò†Â∞ÑË°®
    this.openKeysMap = JSON.parse(sessionStorage.getItem("openKeysMap")) || {}; //ÈôÜÊØÖÂíåÂ±ïÂºÄËèúÂçïkeyÁöÑÊò†Â∞ÑË°®
    // console.log(this.openKeysMap);
    // console.log(this.$route.path);

    return {
      selectedKeys: this.selectedKeysMap[this.$route.path],
      openKeys: this.openKeysMap[this.$route.path]
    };
  },
  mounted() {},
  watch: {
    //ÁÇπÂáªÁà∂Á∫ßËèúÂçïÊó∂Ê≤°ÊúâËß¶Âèë ‚Äú$route.path‚Äù Êï∞ÊçÆÂèòÂåñ
    "$route.path": function(val) {
      this.$emit("getMetaInfo", this.$route.meta);
      this.selectedKeys = this.selectedKeysMap[val];
      this.openKeys = this.openKeysMap[val];
    }
  },

  computed: {
    menuData() {
      // console.log(this.$store.state.addRoutes.rootRoute);
      return this.getMenuData(this.$store.state.addRoutes.rootRoute);
    },
    rootSubmenuKeys() {
      const rootSubmenuKeys = [];
      this.menuData.forEach(item => {
        rootSubmenuKeys.push(item.path);
      });
      return rootSubmenuKeys;
    }
  },
  created() {
    this.$store.commit("gettedInfo", false);
  },
  methods: {
    onOpenChange(openKeys) {
      const latestOpenKey = openKeys.find(
        key => this.menuData[0].path.indexOf(key) === -1
      );
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },
    getMenuData(routes = [], parentKeys = [], selectedKeys) {
      this.$emit("getMetaInfo", this.$route.meta);
      const menuData = [];
      for (const item of routes) {
        if (item) {
          this.openKeysMap[item.path] = parentKeys;
          this.selectedKeysMap[item.path] = [selectedKeys || item.path];
          const newItem = { ...item };
          delete newItem.children;
          if (item.children && !item.hideChildrenInMenu) {
            newItem.children = this.getMenuData(item.children, [
              ...parentKeys,
              item.path
            ]);
          } else {
            this.getMenuData(
              item.children,
              selectedKeys ? parentKeys : [...parentKeys, item.path],
              selectedKeys || item.path
            );
          }
          menuData.push(newItem);
        } else if (item.children) {
          menuData.push(
            ...this.getMenuData(item.children, [...parentKeys, item.path])
          );
        }
      }
      sessionStorage.setItem("openKeysMap", JSON.stringify(this.openKeysMap));
      sessionStorage.setItem(
        "selectedKeysMap",
        JSON.stringify(this.selectedKeysMap)
      );
      this.selectedKeys = JSON.parse(sessionStorage.getItem("selectedKeysMap"))[
        this.$route.path
      ];
      this.openKeys = JSON.parse(sessionStorage.getItem("openKeysMap"))[
        this.$route.path
      ];
      return menuData;
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ant-menu-item-selected {
  background-color: #409fff !important;
}
</style>
